<template>
  <div class="loginWrap">
      <header>
          <router-link to="/home">
          <i class="el-icon-arrow-left"></i>
          </router-link>
          <div>密码登录</div>
      </header>
      <div class="form">
          <section class="input_c">
              <input type="text" placeholder="账号" v-model="username">
          </section>
          <section class="input_c">
              <input :type="value?'text':'password'" placeholder="密码" v-model="password">
              <el-switch
              v-model="value"
              active-color="#13ce66"
              inactive-color="#909399">
              </el-switch>

          </section>
          <section class="input_c">
              <input type="text" placeholder="验证码" v-model="capCode">
              <div>
                  <img :src="capUrl" alt="">
                  <div @click="changeCap">
                      <p>看不清</p>
                      <p>换一张</p>
                  </div>
              </div>
          </section>
      </div>
      <p class="tips">
        温馨提示：未注册过的账号，登录时将自动注册
    </p>
      <p class="tips">
        注册过的用户可凭账号密码登录
    </p>
      <div class="loginBtn" @click="loginAction(username)">登录</div>
      <router-link to="/forget">重置密码?</router-link>
  </div>
</template>

<script>
export default {
name:"Login",
data(){
    return{
        value:"false",
        username:"",
        password:"",
        capCode:"",
        capUrl:""
    }
},
created(){
    this.requestCode();
},
methods:{
    loginAction(username){
        this.axios.post("/v2/login",{
            username:this.username,
            password:this.password,
            captcha_code:this.capCode
        }).then(res=>{
            console.log(res)
            if (res.username != undefined) {
            this.$router.push('/profile')
            localStorage.setItem("username",res.username) 
            localStorage.setItem("userid",res.user_id)
        } else if(res.message == '密码错误') {
            alert("密码错误")
        } else if(res.message == '验证码不正确') {
            alert("验证码不正确")
        }
        }).catch(err => {})
    },
    requestCode(){
        this.axios.post("/v1/captchas").then(res=>{
        this.capUrl = res.code;
    }).catch(err => {})
    },
    changeCap(){
        this.requestCode();
    }
}
}
</script>

<style>
.loginWrap{
    padding-top: 1.95rem;
}
header{
    background-color: #3190e8;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    width: 100%;
    height: 1.95rem;
}
header>div{
    width: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 50%;
    color: #fff;
    text-align: center;
    font-size: .8rem;
    font-weight: 700;
}
header>a{
    position: absolute;
    left: .4rem;
    top: 50%;
    transform: translateY(-50%);
    color: #fff;
}
.form{
    background-color: #fff;
    margin-top: .6rem;
}
.input_c{
    display: flex;
    justify-content: space-between;
    padding: .6rem .8rem;
    border-bottom: 1px solid #f1f1f1;
}
.input_c input{
    font-size: .7rem;
    color: #666;
    border: none;
    outline: none;
}
.input_c>div{
    display: flex;
    align-items: center;
}
.input_c>div p{
    font-size: .55rem;
    color: #666;
}
.input_c>div p+p{
    color: #3b95e9;
    margin-top: .2rem;
}
.input_c>div img{
    width: 3.5rem;
    height: 1.5rem;
    margin-right: .2rem;
}
.tips{
    font-size: .5rem;
    color: red;
    padding: .4rem .6rem;
    line-height: .5rem;
}
.loginBtn{
    margin: 0 .5rem 1rem;
    font-size: .7rem;
    color: #fff;
    background-color: #4cd964;
    padding: .5rem 0;
    border: 1px;
    border-radius: .15rem;
    text-align: center;
}
.loginWrap>*:last-child{
    float: right;
    font-size: .6rem;
    color: #3b95e9;
    margin-right: .3rem;
    text-decoration: none;
}
</style>